
import Search from 'vux/src/components/search/index.vue'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'

export default {
components: {
Search,
InlineLoading
},
data() {
return {
pageIndex: 1,
pageSize: 15,
list: [],
value: '', //输入框值
nodata: false, //无搜索结果
history: '', //历史记录
historyShow: false 
};
},
mounted() {
this.getHistory();
this.$refs.searchinput.focus() 
},
watch: {
value: function() {    //搜索框输入限制
this.value =  this.value.replace(/[^A-Za-z0-9\u4e00-\u9fa5]/g, '');
}
},
methods: {

removeValue() {
this.value = '';
this.$refs.searchinput.focus();
this.nodata = false;
this.list = [];
this.pageIndex = 1;
},

showHistoryJuge() {
if (this.value.toString().length < 1 && this.history.length > 0) {
this.historyShow = true;
} else {
this.historyShow = false;
}
},

getHistory() {
if (window.localStorage.history_drug) {
this.history = window.localStorage.history_drug
let noRepeatArry = this.history.split(',');
this.history = [...new Set(noRepeatArry.reverse())].reverse().join();
if (this.history.charAt(0) == ',') {
this.history = this.history.substring(1, this.history.length)
}
if (this.history.charAt(this.history.length - 1) == ',') {
this.history = this.history.substring(0, this.history.length - 1)
}
localStorage.setItem('history_drug', this.history)
} else {
localStorage.setItem('history_drug', '')
}
},

removeHistory() {
this.history = '';
localStorage.setItem('history_drug', '')
this.showHistoryJuge()
},

setHistory() {
this.historyShow = false;
if (this.value != '') {
let noRepeatArry = this.history.split(',');
noRepeatArry.push(this.value);
this.history = [...new Set(noRepeatArry.reverse())].reverse().join();
if (this.history.charAt(0) == ',') {
this.history = this.history.substring(1, this.history.length)
}
if (this.history.charAt(this.history.length - 1) == ',') {
this.history = this.history.substring(0, this.history.length - 1)
}
localStorage.setItem('history_drug', this.history)
}
},

setseartch(val) {
this.pageIndex = 1;
this.value = val;
this.initdata();
},

changeSearch() {
this.pageIndex = 1;
this.list = [];
this.nodata = false;
this.initdata();
},
initdata() {
this.value =  this.value.replace(/[^A-Za-z0-9\u4e00-\u9fa5]/g, '');
this.showHistoryJuge()
if (this.value != '') {
this.$ajax.post(this.$root.host, {
'MSH.1': 'drugpool',
'MSH.2': 'listDrug',
"pageIndex": this.pageIndex,
"pageSize": this.pageSize,
'title': this.value,
'typeBm': window.localStorage.lastcodeduge_Y && window.localStorage.lastcodeduge_Y != 'undefined' ? window.localStorage.lastcodeduge_Y : '' //二级编码
}).then(data => {
console.log(data)
this.$refs.scroll.finishPullUp();
this.$refs.scroll.finishPullDown();
if (typeof data != 'undefined') { //判断是否空对象
if (typeof data.rows != 'undefined' && data.rows.length > 0) {
this.nodata = false;
if (this.pageIndex == 1) {
this.list = data.rows
} else {
this.list = this.list.concat(data.rows);
}
} else {
this.judge()
}
} else {
this.judge()
}
})
} else {
this.list = [];
this.pageIndex = 1;
this.nodata = false;
}
},
judge() { //无数据判断
if (this.pageIndex > 1) {
this.$vux.toast.text('没有更多了', 'bottom');
} else {
this.nodata = true;
this.list = [];
this.pageIndex = 1;
}
},
pullingUp() {
this.pageIndex++;
this.initdata();
},
toDetail(name, code) { //点击跳转
this.$router.push({
name: "drugsDetails",
params: {
code: code,
name: name,
}
});
},
getfucs(){
this.$refs.searchinput.focus() 
}
}
};



